@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
body {
   margin: 0;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
      'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
      'Helvetica Neue', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}

code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
}

* {
   margin: 0;
   font-family: 'Poppins', sans-serif;
}

html {
   overflow-x: hidden;
}

body {
   --primary: #333647;
   --secondary: #252734;
   --card: #434657;
   --accent: #fcc25b;
   --dark-accent: #ffae29;
   --text: #eaeaeb;
   --light-text: #a7a5a5;
   background-color: var(--secondary);
   color: var(--text);
   text-transform: lowercase;
   overflow-x: hidden;
}

.header {
   color: var(--light-text);
   text-transform: uppercase;
   letter-spacing: 0.13rem;
   font-size: 0.875rem;
}

.title {
   font-size: 1.475rem;
   margin-top: 16px;
   margin-bottom: 32px;
}

.body {
   color: var(--light-text);
   font-size: 0.875rem;
}

.button {
   font-size: 1.125rem;
   color: var(--accent);
   text-decoration: underline;
   cursor: pointer;
}

.button a {
   cursor: pointer;
   color: inherit;
}

.light-text {
   color: var(--light-text);
}

/* Media Queries */
@media screen and (max-width: 768px) {
   .header {
      font-size: 0.75rem;
   }

   .title {
      font-size: 1.275rem;
   }

   .body {
      font-size: 0.75rem;
   }

   .button {
      font-size: 1rem;
   }
}

.navbar-container {
   background-color: var(--primary);
   width: 100%;
   height: 10vh;
   position: fixed;
   top: 0;
   left: 0;
   z-index: 100;
}

.navbar {
   max-width: 1100px;
   margin: auto;
   position: relative;
   width: 100%;
   height: 100%;
   background-color: var(--primary);
   display: flex;
   justify-content: space-between;
   align-items: center;
}

.hamburger {
   display: none;
   cursor: pointer;
}

.hamburger div {
   height: 3px;
   width: 25px;
   background-color: var(--text);
   margin: 5px;
   transition: all 0.3s ease;
}

.toggle .line1 {
   transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .line2 {
   opacity: 0;
}

.toggle .line3 {
   transform: rotate(45deg) translate(-5px, -6px);
}

.navbar-links {
   margin-right: 30px;
   width: 40%;
   display: flex;
   justify-content: space-between;
   font-size: 1.275rem;
   color: var(--text);
}

.navbar-links li {
   list-style: none;
   cursor: pointer;
}

/* Media Queries */
@media screen and (max-width: 768px) {
   .navbar-container {
      padding: 0px;
   }

   .hamburger {
      display: block;
      margin-right: 30px;
   }

   .navbar-links {
      display: none;
      background-color: var(--card);
      width: 100%;
      position: absolute;
      top: 10vh;
      left: 0px;
      right: 0px;
      padding: 0px;
      margin: 0px;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      font-size: 1rem;
      height: 25vh;
   }

   .navbar-active {
      display: flex;
   }
}

.home-container {
   background-color: var(--primary);
   min-height: 70vh;
   padding: 10vh 50px 0px 50px;
}

.home {
   max-width: 1100px;
   margin: auto;
   display: grid;
   grid-template-columns: 60% 40%;
   background-color: var(--primary);
}

.home-left {
   display: flex;
   flex-direction: column;
   justify-content: space-evenly;
   position: relative;
}

.home-left img {
   position: absolute;
   bottom: 0;
   right: 2rem;
   max-height: 100%;
   height: auto;
   width: auto;
   z-index: 0;
   filter: drop-shadow(5px 15px 10px rgba(0, 0, 0, 0.4));
   -webkit-filter: drop-shadow(5px 15px 10px rgba(0, 0, 0, 0, 4));
   object-fit: inherit;
}

.home-left-name {
   z-index: 10;
   font-size: 5rem;
   font-weight: 600;
}

.home-left-last-name {
   margin-top: -36px;
}

.home-left-line {
   margin-top: 16px;
   width: 72px;
   height: 8px;
   background-color: var(--accent);
}

.home-left-links {
   z-index: 10;
   display: flex;
   width: 120px;
   justify-content: space-between;
}

.home-left-links a {
   color: inherit;
   text-decoration: inherit;
}

.home-left-link {
   cursor: pointer !important;
}

.home-right {
   height: 70vh;
   display: flex;
   flex-direction: column;
   justify-content: center;
}

/* Media Queries */
@media screen and (max-width: 768px) {
   .home-container {
      padding: 10vh 30px 0px 30px;
      height: 80vh;
   }

   .home {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
   }

   .home-left {
      margin-top: 1rem;
      width: 100%;
      order: 2;
      height: 40vh;
   }

   .home-left img {
      right: -1rem;
   }

   .home-left-name {
      font-size: 3rem;
   }

   .home-left-last-name {
      margin-top: -16px;
   }

   .home-right {
      order: 1;
      width: 100%;
      height: auto;
      margin-top: 2rem;
   }
}

.contact-container {
   background-color: var(--secondary);
   height: 70vh;
   padding: 0px 50px;
}

.contact {
   max-width: 1100px;
   padding-top: 7rem;
   margin: auto;
   display: grid;
   grid-template-columns: 50% 50%;
   background-color: var(--secondary);
}

.contact-left {
   display: flex;
   flex-direction: column;
   padding-right: 7rem;
   justify-content: flex-start;
}

.contact-right {
   display: flex;
   flex-direction: column;
}

.quotes {
   font-size: 10rem;
}

/* Media Queries */
@media screen and (max-width: 768px) {
   .contact-container {
      padding: 0px 30px;
      height: 80vh;
   }

   .contact {
      padding-top: 2rem;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: flex-start;
   }

   .contact-left {
      margin-top: 1rem;
      width: 100%;
      order: 2;
      height: 40vh;
   }

   .contact-right {
      order: 1;
      width: 100%;
      height: auto;
   }
}

.projects-container {
   position: relative;
   background-color: var(--primary);
   min-height: 70vh;
   padding: 0px 50px;
}

.skills {
   position: absolute;
   left: 0;
   right: 0;
   max-width: 1100px;
   padding: 0px 50px;
   margin: auto;
   display: flex;
   margin-top: -150px;
   justify-content: space-between;
}

.projects {
   max-width: 1100px;
   padding-top: 13rem;
   margin: auto;
   background-color: var(--primary);
}

.languages {
   display: flex;
   flex-wrap: wrap;
   align-items: center;
   justify-content: center;
   padding: 1rem 2rem 1rem 2rem;
}

.languages img {
   margin: 1rem;
   max-height: 40px;
   max-width: 120px;
}

.projects-list-container {
   margin-top: 4rem;
   -webkit-column-count: 2;
           column-count: 2;
   grid-column-gap: 5rem;
   -webkit-column-gap: 5rem;
           column-gap: 5rem;
}

.projects-list-container > .button {
   margin: 6rem 0px;
}

/* Media Queries */
@media screen and (max-width: 768px) {
   .projects-container {
      padding: 0px 30px;
   }

   .skills {
      display: none;
   }

   .projects {
      padding-top: 2rem;
   }

   .languages {
      padding: 1rem 0;
   }

   .languages img {
      margin: 0.75rem;
      max-height: 25px;
      max-width: 75px;
   }

   .projects-list-container {
      margin-top: 2rem;
      -webkit-column-count: 1;
              column-count: 1;
      grid-column-gap: 5rem;
      -webkit-column-gap: 5rem;
              column-gap: 5rem;
   }

   .projects-list-container > .button {
      margin: 3rem 0px;
   }
}

.skill-container {
   width: 25%;
   height: 20rem;
   background-color: var(--card);
   cursor: pointer;
   display: flex;
   align-items: flex-end;
}

.skill-container:hover {
   background-color: var(--dark-accent);
}

.skill-padding {
   display: flex;
   align-items: flex-start;
   justify-content: space-between;
   margin: 2rem;
   font-size: 1.42rem;
}

.skill-icon {
   font-size: 2rem !important;
   margin-right: 2rem;
}

.project-container {
   background-color: var(--primary);
   width: 100%;
   margin: 0rem auto 5rem auto;
   display: inline-block;
   height: 600px;
   cursor: pointer;
   position: relative;
   overflow: hidden;
}

.project-front {
   position: absolute;
   background-color: var(--secondary);
   width: 100%;
   height: 100%;
   display: flex;
   justify-content: space-between;
}

.project-title {
   font-size: 1.475rem;
   margin: 2rem 2rem 1rem 2rem;
   font-weight: 400;
}

.project-body {
   font-size: 1rem;
   margin: 2rem 2rem 1rem 2rem;
}

.project-language {
   color: var(--light-text);
   text-transform: uppercase;
   letter-spacing: 0.13rem;
   margin: 0rem 2rem 1rem 2rem;
   font-size: 0.875rem;
}

.project-button {
   font-size: 1.125rem;
   color: var(--accent);
   margin: 2rem;
   text-decoration: underline;
   cursor: pointer;
}

.phone {
   position: absolute;
   top: 7rem;
   width: 80%;
   height: auto;
   transform: translateX(30%) rotate(-20deg);
}

.web {
   position: absolute;
   top: 15rem;
   width: 150%;
   height: auto;
   transform: translateX(-10%) rotate(-20deg);
}

.project-back {
   position: absolute;
   top: 100%;
   background-color: var(--secondary);
   width: 100%;
   height: 100%;
   display: flex;
   flex-direction: column;
   transition: all 0.5s ease-in;
}

.view-details {
   top: 0;
}

/* Media Queries */
@media screen and (max-width: 768px) {
   .project-container {
      margin: 0rem auto 2rem auto;
      height: 500px;
   }

   .project-title {
      font-size: 1.275rem;
   }

   .project-language {
      font-size: 0.75rem;
   }

   .project-button {
      font-size: 1rem;
      margin: 2rem 2rem 2rem 1rem;
   }

   .phone {
      top: 8rem;
   }

   .web {
      top: 12rem;
   }
}

.footer-container {
   background-color: var(--secondary);
   min-height: calc(100vh - 80px);
   padding: 0px 50px;
}

.footer {
   max-width: 1100px;
   min-height: calc(100vh - 80px);
   margin: auto;
   background-color: var(--secondary);
   display: flex;
   flex-direction: column;
   justify-content: space-between;
}

.footer-top {
   padding-top: 4rem;
   display: grid;
   grid-template-columns: 50% 50%;
}

.footer-left {
   padding-right: 4rem;
}

.footer-left > .title {
   margin-top: 0px;
   text-decoration: none;
}

.footer-left > .button {
   margin: 3rem 0px 2rem 0px;
   text-decoration: none;
}

.footer-left > .button > a {
   text-decoration: underline;
}

.footer-right {
   padding-left: 4rem;
}

.achievements-container {
   display: flex;
   flex-direction: column;
}

.achievements-container div:first-child {
   padding-top: 0px;
}

.achievements-container div:last-child {
   border-bottom: none !important;
}

.achievement {
   display: flex;
   justify-content: space-between;
   padding: 1rem 0px;
   border-bottom: 1px solid #979aa3;
}

.achievement-name {
   margin-right: 1rem;
}

.footer-bottom {
   padding-bottom: 3rem;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.footer-message {
   font-size: 0.875rem;
   margin: 1rem 0px 1.5rem 0px;
}

.footer-links {
   z-index: 10;
   display: flex;
   width: 120px;
   justify-content: space-between;
}

.footer-links a {
   color: inherit;
   text-decoration: inherit;
}

.footer-link {
   cursor: pointer !important;
}

/* Media Queries */
@media screen and (max-width: 768px) {
   .footer-container {
      padding: 0px 30px;
   }

   .footer-top {
      padding-top: 2rem;
      display: flex;
      flex-direction: column;
   }

   .footer-left {
      order: 2;
      padding-right: 0rem;
      padding-bottom: 2rem;
   }

   .footer-left > .button {
      margin: 3rem 0px 2rem 0px;
   }

   .footer-left > .button > a {
      text-decoration: underline;
   }

   .footer-right {
      order: 1;
      padding-left: 0rem;
      padding-bottom: 2rem;
   }

   .achievements-container {
      display: flex;
      flex-direction: column;
   }

   .achievements-container div:first-child {
      padding-top: 0px;
   }

   .achievements-container div:last-child {
      border-bottom: none !important;
   }

   .achievement {
      display: flex;
      justify-content: space-between;
      padding: 1rem 0px;
      border-bottom: 1px solid #979aa3;
   }

   /* .achievement-name {
      margin-right: 1rem;
   } */

   .footer-bottom {
      padding-bottom: 2rem;
   }
}

