.footer-container {
   background-color: var(--secondary);
   min-height: calc(100vh - 80px);
   padding: 0px 50px;
}

.footer {
   max-width: 1100px;
   min-height: calc(100vh - 80px);
   margin: auto;
   background-color: var(--secondary);
   display: flex;
   flex-direction: column;
   justify-content: space-between;
}

.footer-top {
   padding-top: 4rem;
   display: grid;
   grid-template-columns: 50% 50%;
}

.footer-left {
   padding-right: 4rem;
}

.footer-left > .title {
   margin-top: 0px;
   text-decoration: none;
}

.footer-left > .button {
   margin: 3rem 0px 2rem 0px;
   text-decoration: none;
}

.footer-left > .button > a {
   text-decoration: underline;
}

.footer-right {
   padding-left: 4rem;
}

.achievements-container {
   display: flex;
   flex-direction: column;
}

.achievements-container div:first-child {
   padding-top: 0px;
}

.achievements-container div:last-child {
   border-bottom: none !important;
}

.achievement {
   display: flex;
   justify-content: space-between;
   padding: 1rem 0px;
   border-bottom: 1px solid #979aa3;
}

.achievement-name {
   margin-right: 1rem;
}

.footer-bottom {
   padding-bottom: 3rem;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.footer-message {
   font-size: 0.875rem;
   margin: 1rem 0px 1.5rem 0px;
}

.footer-links {
   z-index: 10;
   display: flex;
   width: 120px;
   justify-content: space-between;
}

.footer-links a {
   color: inherit;
   text-decoration: inherit;
}

.footer-link {
   cursor: pointer !important;
}

/* Media Queries */
@media screen and (max-width: 768px) {
   .footer-container {
      padding: 0px 30px;
   }

   .footer-top {
      padding-top: 2rem;
      display: flex;
      flex-direction: column;
   }

   .footer-left {
      order: 2;
      padding-right: 0rem;
      padding-bottom: 2rem;
   }

   .footer-left > .button {
      margin: 3rem 0px 2rem 0px;
   }

   .footer-left > .button > a {
      text-decoration: underline;
   }

   .footer-right {
      order: 1;
      padding-left: 0rem;
      padding-bottom: 2rem;
   }

   .achievements-container {
      display: flex;
      flex-direction: column;
   }

   .achievements-container div:first-child {
      padding-top: 0px;
   }

   .achievements-container div:last-child {
      border-bottom: none !important;
   }

   .achievement {
      display: flex;
      justify-content: space-between;
      padding: 1rem 0px;
      border-bottom: 1px solid #979aa3;
   }

   /* .achievement-name {
      margin-right: 1rem;
   } */

   .footer-bottom {
      padding-bottom: 2rem;
   }
}
